import Layout from "../components/layout"
import Seo from "../components/seo"

const ContactUs = () => {
  // const storeInfo = data.allMarkdownRemark.nodes[0].frontmatter
  return (
    <Layout>
      <Seo title="Contact Us" />
      <section className="page-section">
        <div className="container cta">
          <div className="row">
            <div className="col-xl-9 mx-auto">
              <div className="cta-inner bg-faded rounded">
                <h2 className="section-heading mb-5 text-center">
                  <span className="section-heading-upper">Send us a note</span>
                  <span className="section-heading-lower">Contact Us</span>
                </h2>
                <form id="contactform" method="POST">
                  <div class="mb-3">
                    <label htmlFor="name" class="form-label">
                      Name
                    </label>
                    <input
                      id="name"
                      type="text"
                      className="form-control"
                      placeholder="Your name"
                    />
                  </div>
                  <div class="mb-3">
                    <label htmlFor="email" class="form-label">
                      Email
                    </label>
                    <input
                      type="email"
                      id="email"
                      placeholder="Your email"
                      className="form-control"
                    />
                  </div>
                  <div class="mb-3">
                    <label htmlFor="message" class="form-label">
                      Message
                    </label>
                    <textarea
                      rows="4"
                      id="message"
                      placeholder="Your message to us"
                      className="form-control"
                    />
                  </div>
                  <div className="intro-button mx-auto d-flex justify-content-end">
                    <button
                      type="submit"
                      style={{ fontSize: "1em" }}
                      className="btn btn-lg btn-primary mb-3 d-flex align-items-center"
                    >
                      <span className="me-1">Send</span>
                      <svg
                        xmlns="http://www.w3.org/2000/svg"
                        width="16"
                        height="16"
                        fill="currentColor"
                        class="bi bi-envelope-fill"
                        viewBox="0 0 16 16"
                      >
                        <path d="M.05 3.555A2 2 0 0 1 2 2h12a2 2 0 0 1 1.95 1.555L8 8.414.05 3.555zM0 4.697v7.104l5.803-3.558L0 4.697zM6.761 8.83l-6.57 4.027A2 2 0 0 0 2 14h12a2 2 0 0 0 1.808-1.144l-6.57-4.027L8 9.586l-1.239-.757zm3.436-.586L16 11.801V4.697l-5.803 3.546z" />
                      </svg>
                    </button>
                  </div>
                </form>
              </div>
            </div>
          </div>
        </div>
      </section>
    </Layout>
  )
}

export default ContactUs

